import React, {useState, useEffect} from 'react';
import ToDoList from './ToDoList';
import FileCopyIcon from '@material-ui/icons/FileCopy';

const App = () =>{
    
    var myListFromLS = JSON.parse(window.localStorage.getItem('myList'));   
    
    if(myListFromLS === null){
        window.localStorage.setItem('myList', JSON.stringify([]));               // just for creating a storage.     
        myListFromLS = JSON.parse(window.localStorage.getItem('myList'));
    }
        
    const [listItem, setListItem] = useState("");
    const [itemArr, setItemArr]   = useState(myListFromLS);
    
    useEffect(() =>{
        window.localStorage.setItem('myList', JSON.stringify(itemArr));
    }, [itemArr]);
        
    
    
    const onchangeFunc = (event) =>{
            setListItem(event.target.value);
    };
    
    
    const addItem = () =>{
        setItemArr((oldArr) =>{
            
            if(listItem !== "")
                return([...oldArr, listItem]);
            else
                return([...oldArr]);
            
        });
        setListItem("");
    };
    /******* window.localStorage.setItem('myList', JSON.stringify(itemArr));  *******/
//    const deleteFunc = (id) =>{
//        setItemArr(  (oldArr) =>{
//            return oldArr.filter((arrElem, index)=>{
//                return index !== id;
//            });   
//        });
//    };
    
    const deleteFunc = (id) =>{
        
        setItemArr((oldArr) =>{
            oldArr.splice(id, 1);
            return ([...oldArr]);
        });
        
    };
    /******* window.localStorage.setItem('myList', JSON.stringify(itemArr)); *******/
    
    const copyList = () =>{
        var dummy = document.createElement("textArea");
        document.body.appendChild(dummy);
        dummy.value = itemArr.toString();
        dummy.select();
        document.execCommand("copy");
        document.body.removeChild(dummy);
        alert("List Copied !");
    }
    
    
    return(
         <>
        <div className="main-div">
            <div className="center-div">
                <h1>ToDo List</h1>
                <br />
                <input type="text" placeholder="Add item" onChange={onchangeFunc} value={listItem}></input>
                <button className="addBtn" onClick={addItem}> + </button>
                <ol>
                    { itemArr.map((itemVal, index) =>{
                            return <ToDoList
                                       
                                       text     = {itemVal}
                                       key      = {index}
                                       id       = {index}
                                       onSelect = {deleteFunc}
                                    
                                       />;
                    })}
                
                </ol>
                <div className = "copyDiv" onClick = {copyList}>
                     COPY
                </div>
            </div>
        </div>
        </>
        
    );
};  

export default App;






















