import React from 'react';
import ClearIcon from '@material-ui/icons/Clear';

const ToDoList = (props) =>{
    
    
    
    return (
    <>   
        <div className="itemDiv">
            <ClearIcon className="fa fa-times" 
                onClick={() => {
                    
                    props.onSelect(props.id);
                    
                }} />
            <li>{props.text}</li> 
        </div>

    </>
    );
    
};

export default ToDoList;
      